import { mapState, mapGetters } from 'vuex'
export default {
  name: "login",
  layout: "userlayout",
  middleware: ['auth'],
  computed: {
    ...mapState([
      'auth',
    ])
  },
  data() {
    return {
      expand: false,
      username: 'boss',
      password: 'B@ss#1234',
      passwordShow: false,
    };
  },
  methods: {
    setUserAuth () {
    },
    login () {
      this.$axios.$post('/v1/login', {
          username: this.username,
          password: this.password
      }).then((response) => {
        if (response.status == 'success') {
          this.$nuxt.$store.commit('SET_TOKEN', response.data.accessToken)
          this.$router.push({
            path: '/'
          })
        }
      }).catch((error) => {
          console.log(error);
      })
    },
    viewRoute(link) {
      this.$router.push({
        path: link
      });
    },
    redirectTo(path) {
      this.$router.replace({ path });
    }
  }
};
